  //     
  const calculateExpression = (expression) => {
    const operators = {
      '+': (a, b) => a + b,
      '-': (a, b) => a - b,
      '*': (a, b) => a * b,
      '/': (a, b) => a / b,
    };
  
    const precedence = {
      '+': 1,
      '-': 1,
      '*': 2,
      '/': 2,
    };
  
    const isOperator = (token) => ['+', '-', '*', '/'].includes(token);
  
    const tokens = expression.match(/(\d+|\+|-|\*|\/)/g);
    console.log(' :', tokens);
  
    if (!tokens) {
      throw new Error(' :    ');
    }
  
    const values = [];
    const ops = [];
  
    const applyOperator = () => {
      const b = values.pop();
      const a = values.pop();
      const op = ops.pop();
      console.log(' :', op, '| :', a, b);
      if (a === undefined || b === undefined || !operators[op]) {
        throw new Error(' ');
      }
      values.push(operators[op](a, b));
    };
  
    tokens.forEach((token) => {
      console.log(' :', token);
      if (!isNaN(token)) {
        values.push(parseFloat(token));
        console.log('   :', values);
      } else if (isOperator(token)) {
        while (ops.length && precedence[ops[ops.length - 1]] >= precedence[token]) {
          applyOperator();
        }
        ops.push(token);
        console.log('   :', ops);
      } else {
        throw new Error(' ');
      }
    });
  
    while (ops.length) {
      applyOperator();
    }
  
    if (values.length !== 1) {
      throw new Error(' :    ');
    }
  
    console.log(' :', values[0]);
    return values[0];
  };
  
  export const calculateAge = date => {
    const [day, month, year] = date.split('.');
    const enteredDate = new Date(`${year}-${month}-${day}`);
    const currentDate = new Date();
  
    let age = currentDate.getFullYear() - enteredDate.getFullYear();
    const monthDiff = currentDate.getMonth() - enteredDate.getMonth();
  
    if (monthDiff < 0 || (monthDiff === 0 && currentDate.getDate() < enteredDate.getDate())) {
      age--;
    }
  
    return age;
  };

