import React, { useState } from 'react';
import styled from 'styled-components';

const ModalOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
`;

const OptionsList = styled.ul`
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: left; 
`;

const OptionItem = styled.li`
  cursor: pointer;
  padding: 10px;
  color: black;
  font-size: 16px;
  line-height: 1.5;
  transition: background-color 0.3s ease;
  border-bottom: 1px solid #ddd; /*    */

  &:last-child {
    /* border-bottom: none;      */
  }

  &:hover {
    background-color: #f5f5f5; /*     */
  }
`;

const CustomInput = styled.input`
  padding: 10px;
  /* padding-bottom: 0; */
  /* margin-top: 10px; */
  width: 100%;
  box-sizing: border-box;
  border: none;
  outline: none;
  font-size: 16px;
  color: black; /*    */
  line-height: 1.5;

  &::placeholder {
    color: darkorange; /*     */
    font-size: 16px;
    font-style: italic; /*    */
    font-weight: bold; /*     */
  }
  &:hover {
    background-color: #f5f5f5; /*     */
  }
`;

const ModalContent = styled.div`
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  width: 300px;
  text-align: center;
  color: black;
  position: relative;
`;

const OrangeStrong = styled.strong`
  color: orange;
`;

export const InfoModal = ({ onClose, onSelect, options, text, Context}) => {
  const delProfile = (
    <>
      <p>  ,    </p>
      <a href="mailto:KnowMeEggDonor@gmail.com?subject=  " style={{ color: 'black', textDecoration: 'none' }}>
        <strong>KnowMeEggDonor@gmail.com</strong>
      </a>
      <p>   "  ".</p>
    </>
  );

  const viewProfile = (
    <>
      <p>  ,  </p>
      <a href="https://play.google.com/store/apps/details?id=com.xanderkiev.MyApp" style={{ color: 'black', textDecoration: 'none' }}>
        <OrangeStrong>KnowMe: Egg donor</OrangeStrong>
      </a>
      <p> Google Play</p>
    </>
  );
  
  const dotsMenu = (
    <>{Context && <Context/>}
    </>
  );

  //////////////////////////////
  const [customInput, setCustomInput] = useState(''); //    
  const [showCustomInput, setShowCustomInput] = useState(true); //     

  const handleSelect = option => {
    onSelect(option); //   
  };

  const handleCustomInputChange = e => {
    setCustomInput(e.target.value); //     
  };

  const handleConfirm = () => {
    onSelect({ placeholder: customInput }); //   
    setCustomInput(''); //  
    setShowCustomInput(false); //   
  };

  const handleKeyDown = e => {
    if (e.key === 'Enter') {
      handleConfirm();
    }
  };

  const pickerOptions = (
    <>
    {options && <><OptionsList>
        {options.map(option => (
          <OptionItem key={option.placeholder} onClick={() => handleSelect(option)}>
            {option.placeholder} / {option.ukrainian}
          </OptionItem>
        ))}
      </OptionsList>
      {showCustomInput && (
        // <CustomInputContainer>
        <CustomInput
          type="text"
          value={customInput}
          onChange={handleCustomInputChange}
          placeholder=" "
          onBlur={handleConfirm}
          onKeyDown={handleKeyDown}
        />)}</>}
    </>
  );

  return (
    <ModalOverlay onClick={onClose}>
      <ModalContent>
        {text === 'delProfile' && delProfile}
        {text === 'viewProfile' && viewProfile}
        {text === 'pickerOptions' && pickerOptions}
        {text === 'dotsMenu' && dotsMenu}
      </ModalContent>
    </ModalOverlay>
  );
};

export default InfoModal;
