export const getUrlofUploadedAvatar = async (photo, userId) => {
  const compressedPhoto = await compressPhoto(photo, 50); //    50 
  const file = await getFileBlob(compressedPhoto); //     ' Blob

  const uniqueId = Date.now().toString(); //   "  
  const fileName = `${uniqueId}.jpg`; //   '  
  const linkToFile = ref(storage, `avatar/${userId}/${fileName}`); //        Firebase
  await uploadBytes(linkToFile, file); //  
  const url = await getDownloadURL(linkToFile); //  URL-  
  return url;
};

export const getUrlofUploadedPhoto = async photo => {
  const file = await getFileBlob(photo); //     ' Blob
  const uniqueId = Date.now().toString(); //   "  
  const linkToFile = ref(storage, `imgPost/${uniqueId}`); //        Firebase
  await uploadBytes(linkToFile, file); //  
  const url = await getDownloadURL(linkToFile); //  URL-  
  return url;
};

const getFileBlob = file => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onloadend = () => {
      resolve(new Blob([reader.result], { type: file.type }));
    };
    reader.onerror = reject;
    reader.readAsArrayBuffer(file);
  });
};

const compressPhoto = (file, maxSizeKB) => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = event => {
      const img = new Image();
      img.onload = () => {
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');

        //    canvas,  
        let width = img.width;
        let height = img.height;

        //    1000px  ,   1000px
        const MAX_WIDTH = 1000;
        if (width > MAX_WIDTH) {
          height *= MAX_WIDTH / width;
          width = MAX_WIDTH;
        }

        canvas.width = width;
        canvas.height = height;

        //    canvas   
        ctx.drawImage(img, 0, 0, width, height);

        //      0.6
        let quality = 0.6;
        let compressedDataUrl = canvas.toDataURL('image/jpeg', quality);
        let compressedFile = dataURLToFile(compressedDataUrl);

        //         
        while (compressedFile.size > maxSizeKB * 1024 && quality > 0.1) {
          quality -= 0.1; //  
          compressedDataUrl = canvas.toDataURL('image/jpeg', quality);
          compressedFile = dataURLToFile(compressedDataUrl);
        }

        console.log('   :', compressedFile.size);
        resolve(compressedFile);
      };
      img.onerror = reject;
      img.src = event.target.result; //    ' Image
    };
    reader.onerror = reject;
    reader.readAsDataURL(file); //    Data URL  canvas
  });
};

//    dataURL  
const dataURLToFile = dataUrl => {
  const arr = dataUrl.split(',');
  const mime = arr[0].match(/:(.*?);/)[1];
  const bstr = atob(arr[1]);
  let n = bstr.length;
  const u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new File([u8arr], 'compressed.jpg', { type: mime });
};

export const fetchUserData = async userId => {
  const userRef = doc(db, 'users', userId);
  const docSnap = await getDoc(userRef);
  const existingData = docSnap.data();
  return { existingData, userRef };
};

export const fetchUsersCollection = async () => {
  //   
  const usersCollection = collection(db, 'users');
  const querySnapshot = await getDocs(usersCollection);
  const database = querySnapshot.docs.map(doc => doc.data());
  // console.log('userDataArray!!!!!!! :>> ', userDataArray);
  return database;
};

export const fetchUsersCollectionInRTDB = async () => {
  //   ",    
  const db = getDatabase();
  const usersRef = ref2(db, 'users');
  //    
  const snapshot = await get(usersRef);
  if (snapshot.exists()) {
    const data = snapshot.val();
    //  '  
    const dataArray = Object.keys(data).map(key => data[key]);
    return dataArray;
  } else {
    return []; //   ,   
  }
};

