const filterByUserRole = (value) => {
  const excludedRoles = ['ag', 'ip']; // ,   
  return !excludedRoles.includes(value.userRole);
};

//    
const filterByNegativeBloodType = (value) => {
  if (!value.blood) return true; // ,     
  const negativeBloodTypes = ['1-', '2-', '3-', '4-', '-']; //   
  return !negativeBloodTypes.includes(value.blood);
};

//       ()
const filterByAgeAndMaritalStatus = (value, ageLimit = 30, requiredStatus = "Yes") => {
  if (!value.birth || !value.maritalStatus) return true; // ,   
  const birthDate = value.birth.split('.');
  const birthYear = parseInt(birthDate[2], 10);
  const currentYear = new Date().getFullYear();
  const age = currentYear - birthYear;

  return !(value.maritalStatus === requiredStatus && age > ageLimit); // ,     30 
};

//   csection
const filterByCSection = (value) => {
  if (!value.csection) return true; // ,   
  return value.csection !== '2' && value.csection !== '3';
};

//   
const filterMain = (usersData) => {
  let excludedUsersCount = 0; //   

  const filteredUsers = Object.entries(usersData).filter(([key, value]) => {
    const filters = {
      filterByAgeAndMaritalStatus: filterByAgeAndMaritalStatus(value, 30, "Yes"), //    
      filterByUserRole: filterByUserRole(value),                                 //    
      filterByNegativeBloodType: filterByNegativeBloodType(value),               //    
      filterByCSection: filterByCSection(value),                                 //   csection
    };

    const failedFilters = Object.entries(filters).filter(([filterName, result]) => !result);

    if (failedFilters.length > 0) {
      console.log(`User excluded by filter: ${key}`);
      failedFilters.forEach(([filterName]) => {
        console.log(`Failed filter: ${filterName}`);
      });
      excludedUsersCount++; //    
    }

    return failedFilters.length === 0;
  });

  console.log(`Total excluded users: ${excludedUsersCount}`); //     

  return filteredUsers;
};

//      (dd.mm.)
function isValidDate(dateString) {
  //     
  const dateRegex = /^\d{2}\.\d{2}\.\d{4}$/;
  return dateRegex.test(dateString);
}

// 
const sortUsers = (filteredUsers) => {
  const today = new Date().toLocaleDateString('uk-UA'); // ".."

  return filteredUsers.sort(([_, a], [__, b]) => {
    console.log('a :>> ', a);
    console.log('a.getInTouch :>> ', a.getInTouch);
    const aDate = a[1].getInTouch || "99.99.9999";
    const bDate = b[1].getInTouch || "99.99.9999";

    //     "" 
    if (aDate === today && bDate !== today) return -1;
    if (bDate === today && aDate !== today) return 1;

    //     (, "99.99.9999"),     
    if (aDate === "99.99.9999" && bDate !== "99.99.9999") return 1;
    if (bDate === "99.99.9999" && aDate !== "99.99.9999") return -1;

    //     Date  
    if (isValidDate(aDate) && isValidDate(bDate)) {
      const aDateParts = aDate.split('.');
      const bDateParts = bDate.split('.');
      const aParsedDate = new Date(aDateParts[2], aDateParts[1] - 1, aDateParts[0]);
      const bParsedDate = new Date(bDateParts[2], bDateParts[1] - 1, bDateParts[0]);

      return aParsedDate - bParsedDate;
    }
    return 0; //      
  });
};

export const fetchPaginatedNewUsers = async (lastKey) => {
  const db = getDatabase();
  const usersRef = ref2(db, 'newUsers');

  try {


//   ,      
const allUsersQuery = query(usersRef, orderByChild('getInTouch'), startAt(null));
const allUsersSnapshot = await get(allUsersQuery);
const allUsers = allUsersSnapshot.exists() ? Object.entries(allUsersSnapshot.val()) : [];

//     
const sortedUsers2 = allUsers.filter(([key, value]) => {
  const getInTouch = value.getInTouch;

  //   getInTouch    ,  true (  )
  if (!getInTouch  || !isValidDate(getInTouch)) {
    return true;
  }

  if (getInTouch === '99.99.2099' || getInTouch === '99.99.9999') {
    return false;
  }
  //     Date  
  const dateParts = getInTouch.split('.');
  const date = new Date(dateParts[2], dateParts[1] - 1, dateParts[0]);
  //     true    (   )
  return date <= new Date();
});

// 3. '  ( )
const combinedUsers = sortedUsers2;

    // 5.  
    const filteredUsers = filterMain(combinedUsers);

    // 6.   :  ->   -> 
    const sortedUsers = sortUsers(filteredUsers);

    // 7.   [key, value]   '
    const paginatedUsers = sortedUsers.slice(0, 10).reduce((acc, [key, value]) => {
      const userId = value[0]; //    - userId
      const userData = value[1]; //   - '  
      acc[userId] = userData;
      return acc;
    }, {});

    // ,   10 ,     users   userId
    const userIds = Object.keys(paginatedUsers); //   userId  paginatedUsers
    const usersData = {};

    const userPromises = userIds.map((userId) => {
      return fetchUserById(userId);  //  fetchUserById  ,    
    });
  
    // ,    
    const userResults = await Promise.all(userPromises);
  
    //       '
    userResults.forEach((userData, index) => {
      const userId = userIds[index];
      if (userData) {
        usersData[userId] = userData;
      }
    });
  
    // '   newUsers  users   userId
    const finalUsers = userIds.reduce((acc, userId) => {
      const newUserData = paginatedUsers[userId]; //   newUsers
      const userDataFromUsers = usersData[userId] || {}; //   users
      acc[userId] = { ...newUserData, ...userDataFromUsers }; // ' 
      return acc;
    }, {});
  
    const nextKey = sortedUsers.length > 10 ? sortedUsers[10][0] : null;
  
    return {
      users: finalUsers, // '  ,     newUsers  users
      lastKey: nextKey,
      hasMore: sortedUsers.length > 10,
    };
  
  } catch (error) {
    console.error('Error fetching paginated filtered users:', error);
    return {
      users: {},
      lastKey: null,
      hasMore: false,
    };
  }
};

